import request from '@/utils/request'

export default {
  // 收藏课程
  collect(courseId, memberId) {
    return request({
      url: '/eduService/collect',
      method: 'post',
      data: {
        courseId,
        memberId
      }
    })
  },
  // 查询课程收藏状态
  collectStatus(courseId, memberId) {
    return request({
      url: `/eduService/collect/${courseId}/${memberId}`,
      methods: 'get'
    })
  }
}