import request from '@/utils/request'

export default {
  // 获取课程评论分页数据
  getCommentPage(courseId, page, pageSize) {
    return request({
      url: `/eduService/comment/getCommentList/${courseId}/${page}/${pageSize}`,
      method: 'get'
    })
  },
  // 发布评论
  saveComment(comment) {
    return request({
      url: '/eduService/comment/saveComment',
      method: 'post',
      data: comment
    })
  }
}